import React, { useState, useEffect } from "react";
import AWS from "aws-sdk";
import "./AppRecruit.css";

const AppRecruit = () => {
  const [sidebarVisible, setSidebarVisible] = useState(false);
  const [cloudProviderPopupVisible, setCloudProviderPopupVisible] = useState(false);
  const [toastMessage, setToastMessage] = useState("");
  const [toastType, setToastType] = useState("success");
  const [selectedRole, setSelectedRole] = useState("");
  const [selectedCloudProvider, setSelectedCloudProvider] = useState("");
  const [globalSelectedLevel, setGlobalSelectedLevel] = useState("");

  // AWS S3 Configuration
  useEffect(() => {
    AWS.config.update({
      region: "us-east-1", // Update with your region
      credentials: new AWS.CognitoIdentityCredentials({
        IdentityPoolId: "us-east-1:583ab747-d668-4305-8c02-0a7e39d4b791", // Update with your Identity Pool ID
      }),
    });

    new AWS.S3({
      apiVersion: "2006-03-01",
      params: { Bucket: "tagteam-bucket" }, // Update with your bucket name if needed
    });
  }, []);

  // Function to show toast messages
  const showToast = (message, type = "success") => {
    setToastMessage(message);
    setToastType(type);

    // Remove the toast after 3 seconds
    setTimeout(() => {
      setToastMessage("");
    }, 3000);
  };

  // Function to get query parameters
  const getQueryParam = (name) => {
    const urlParams = new URLSearchParams(window.location.search);
    return urlParams.get(name);
  };

  // Function to navigate to a specific page
  const navigateToPage = () => {
    window.location.href = "ECselection.html";
  };

  const navigateTo = (page) => {
    window.location.href = page;
  };

  // Function to handle role selection
  const selectRoleAndOpenPopup = (role, dropdownId) => {
    const dropdown = document.getElementById(dropdownId);
    if (!dropdown.value || dropdown.value === "select") {
      setCloudProviderPopupVisible(true);
      return;
    }
    setSelectedRole(role);
    setGlobalSelectedLevel(dropdown.value);
    showToast(`Selected Role: ${role}, Level: ${dropdown.value}`, "success");
  };

  const closeCloudProviderPopup = () => {
    setCloudProviderPopupVisible(false);
  };

  // Set EC name from query parameters on component mount
  useEffect(() => {
    const ecName = getQueryParam("selectedValue") || "";
    const ecNameInput = document.getElementById("ec-name");
    if (ecNameInput) {
      ecNameInput.value = ecName;
    }
  }, []);

  const toggleSidebar = () => {
    setSidebarVisible(!sidebarVisible);
  };

  return (
    <div>
      {/* Toast Notification */}
      {toastMessage && (
        <div id="toast" className={`toast show ${toastType}`}>
          {toastMessage}
        </div>
      )}

      <button id="toggle-sidebar-btn" onClick={toggleSidebar}>
         Menu
      </button>

      {/* Sidebar */}
      <div className={`sidebar-menu ${sidebarVisible ? "show" : "hidden"}`}>
        <div
          className="sidebar-option active"
          onClick={() => navigateTo("Dashboard.html")}
        >
          <i className="fas fa-tachometer-alt"></i>
          <span>Dashboard</span>
        </div>
        <div
          className="sidebar-option"
          onClick={() => navigateTo("ECselection.html")}
        >
          <i className="fas fa-users"></i>
          <span>Recruit</span>
        </div>
        <div
          className="sidebar-option"
          onClick={() => navigateTo("candidatespage.html")}
        >
          <i className="fas fa-tasks"></i>
          <span>RRF Tracking</span>
        </div>
        <div
          className="sidebar-option"
          onClick={() => navigateTo("GTPrescreening.html")}
        >
          <i className="fas fa-tasks"></i>
          <span>GT's Prescreening</span>
        </div>
        <div
          className="sidebar-option logout-option"
          onClick={() => navigateTo("index.html")}
        >
          <i className="fas fa-sign-out-alt"></i>
          <span>Logout</span>
        </div>
      </div>

      <div className="backbutton" onClick={navigateToPage}>
        <i className="fas fa-arrow-left"></i>
      </div>

      <div className="progress-steps">
        <div className="steps-container">
          <div className="progress-line"></div>
          <div className="step active">
            <div className="step-circle">1</div>
            <div className="step-title">Resume Pre-screen</div>
          </div>
          <div className="step">
            <div className="step-circle">2</div>
            <div className="step-title">Online iMocha</div>
          </div>
          <div className="step">
            <div className="step-circle">3</div>
            <div className="step-title">L2 Technical</div>
          </div>
          <div className="step">
            <div className="step-circle">4</div>
            <div className="step-title">Fitment Round</div>
          </div>
        </div>
      </div>

      <div className="role-selection-container">
        <div className="role-selection-header">
          <h1>Select Your Role for the Interview</h1>
        </div>

        <div className="role-cards">
          {/* Example Role Card */}
          <div className="role-card">
            <h2>Cloud Native Application Engineer - Backend</h2>
            <p>
              Responsible for overseeing the code releases, combining an
              understanding of both engineering and coding.
            </p>
            <h3>Select Cloud Provider and Level:</h3>
            <div style={{ display: "flex", alignItems: "center", gap: "15px" }}>
              <span className="radio-group">
                <label>
                  <input type="radio" name="cloudProvider" value=".Net" /> .Net
                </label>
                <label>
                  <input type="radio" name="cloudProvider" value="Java" /> Java
                </label>
              </span>
              <select id="levelDropdown1">
                <option>select</option>
                <option value="Junior">Junior</option>
                <option value="Senior">Senior</option>
              </select>
            </div>
            <button
              onClick={() =>
                selectRoleAndOpenPopup(
                  "Cloud Native Application Engineer - Backend",
                  "levelDropdown1"
                )
              }
            >
              Select
            </button>
          </div>
        </div>
      </div>

      {/* Cloud Provider Reminder Popup */}
      {cloudProviderPopupVisible && (
        <div id="cloudProviderPopup" className="popupc">
          <div className="popup-contentc">
            <span className="close-btn" onClick={closeCloudProviderPopup}>
              &times;
            </span>
            <p>Please select a cloud provider before proceeding.</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default AppRecruit;